.tag-ctn{
	margin-bottom: 10px;
    width: 100%;
    height: auto !important;
    color: $cbase;
    font-family: $ffbase;
    background-color: $clight;
    border: 1px solid $cline;
    border-radius: 5px;
}
.tag-ctn-invalid{
    border: 1px solid #c00;
}
.tag-ctn-readonly{
    cursor: pointer;
}
.tag-ctn-disabled{
    cursor: not-allowed;
    background-color: #eee;
}
.tag-ctn input{
	flex: 1 0 100%;
    padding: 8px;
    width: 100%;
    height: 22px;
    color: $cbase;
    font-family: $ffbase;
    background-color: transparent;
    border: 0;
    border-radius: 0;
}
.tag-ctn-disabled input{
    cursor: not-allowed;
    background-color: #eee;
}
.tag-ctn .tag-input-readonly{
    cursor: pointer;
}
.tag-ctn .tag-empty-text{
    color: #ddd;
}
.tag-ctn .tag-trigger{
    @extend .flex;
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 40px;
    cursor: pointer;
    z-index: 10;
}
.tag-ctn .tag-trigger .tag-trigger-ico {
    border-top: 5px solid gray;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
}
.tag-ctn-disabled .tag-trigger{
    cursor: not-allowed;
    background-color: #eee;
}
.tag-res-ctn{
    background: #fff;
    overflow-y: auto;
    z-index: 9999;
    border-radius: 3px;
    box-shadow: 0px 1px 3px rgba(#000, .1);
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    transition: border linear 0.2s, box-shadow linear 0.2s;
}
.tag-res-ctn .tag-res-group{
    line-height: 23px;
    text-align: left;
    padding: 2px 5px;
    font-weight: bold;
    border-bottom: 1px dotted #CCC;
    border-top: 1px solid #CCC;
    background: #f3edff;
    color: #333;
}
.tag-res-ctn .tag-res-item{
    line-height: 25px;
    text-align: left;
    padding: 2px 5px;
    color: #666;
    cursor: pointer;
}
.tag-res-ctn .tag-res-item:hover{
	background-color: #f5f5f5;
}
.tag-res-ctn .tag-res-item-grouped{
    padding-left: 15px;
}
.tag-sel-ctn{
	display: flex;
	flex-wrap: wrap;
	padding: 9px 30px 9px 8px;
}
.tag-sel-ctn .tag-sel-item{
	display: flex;
    background: #555;
    color: #eee;
    float: left;
    font-size: 12px;
    padding: 0 5px;
    border-radius: 3px;
    margin-left: 5px;
    margin-top: 4px;
    word-break: break-all;
}
.tag-sel-ctn .tag-sel-text{
    background: #FFF;
    color: #666;
    padding-right: 0;
    margin-left: 0;
    font-size: 14px;
    font-weight: normal;
}
.tag-res-ctn .tag-res-item em{
    font-style: normal;
    background: #565656;
    color: #FFF;
}
.tag-sel-ctn .tag-sel-item:hover{
    background: #565656;
}
.tag-sel-ctn .tag-sel-text:hover{
    background: #FFF;
}
.tag-sel-ctn .tag-sel-item-active{
    border: 1px solid red;
    background: #757575;
}
.tag-ctn .tag-sel-ctn .tag-sel-item{
    margin-top: 3px;
}
.tag-stacked .tag-sel-item{
    float: inherit;
}
.tag-sel-ctn .tag-sel-item .tag-close-btn{
	margin-top: 6px;
    padding-left: 15px;
    width: 7px;
    height: 7px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center -7px;
    cursor: pointer;

}
.tag-helper{
    position: absolute;
    right: 0;
    bottom: -22px;
    color: #aaa;
    font-size: 10px;
}
